/* reset */
* {margin: 0; padding: 0; box-sizing: border-box;}
ul {list-style: none;}
a {color: inherit; text-decoration: none;}
img {max-width: 100%;}
/*메인폰트*/
@font-face {
	font-family: 'SUITE';
	font-weight: 400;
	src: url('./font/SUITE-Regular.woff2') format('woff2');
}
@font-face {
	font-family: 'SUITE';
	font-weight: 500;
	src: url('./font/SUITE-Medium.woff2') format('woff2');
}
@font-face {
	font-family: 'SUITE';
	font-weight: 700;
	src: url('./font/SUITE-Bold.woff2') format('woff2');
}
body {
    font-family: 'SUITE',' sans-serif;';
    color: #fff;
    font-size: 16px;
	width: 100%;
	height: 100vh;
	overflow: hidden;
	scrollbar-width: 17px;
	scrollbar-color: var(--body-bg);
}
/* color */
:root {
	--header-bg: #181818;
	--body-bg: #1f1f1f;
	--line: #2b2b2b;
	--select-point: #0078D4;
	--select-point2: #6a9955;
	--select-point3: #ce764e;
	--select-point4: #9cdcde;
	--select: #fff;
	--none-select : #868686;
}

/* top_header */
.top_header {
	width: 100%;
	height: 42px;
	background-color: var(--header-bg);
	border-bottom: 1px solid var(--line);
	display: flex;
	align-items: center;
	position: fixed;
	top: 0;
	left: 0;
}
.top_header nav ul {
	display: flex;
	padding-left: 11px;
}
.top_header nav ul li {
	margin-right: 20px;
	font-size: 14px;
	cursor: pointer;
}
.top_header nav ul li:first-child div {
	width: 19px;
	height: 19px;
	background-color: antiquewhite;
}
.top_header nav ul li:first-child {
	margin-right: 22px;
}
.top_header > div {
	width: 722px;
	height: 28px;
	background-color: #242424;
	border: 1px solid var(--line);
	border-radius: 7px;
	margin-left: 242px;
	cursor: pointer;
}
.top_header h1 {
	font-weight: 400;
	font-size: 12px;
	line-height: 28px;
	text-align: center;
}

/* left_iconarea */
.left_iconarea {
	width: 58px;
	height: calc(100vh - 42px);
	background-color: var(--header-bg);
	border-right: 1px solid var(--line);
	margin-top: 42px;
	padding-top: 13px;
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 30px;
	overflow: hidden;
	position: fixed;
	left: 0;
	top: 0;
}
.left_iconarea div {cursor: pointer}

/* left_opentab */
.left_opentap {
	width: 307px;
	height: calc(100vh - 42px);
	background-color: var(--header-bg);
	border-right: 1px solid var(--line);
	position: fixed;
	top: 42px;
	left: 58px;
}
.left_opentap .tool {
	width: 100%;
	height: 65px;
	display: flex;
	padding: 0 30px;
	justify-content: space-between;
	align-items: center;
}
.left_opentap .tool h3 {
	font-size: 16px;
	font-weight: 400;
}
.left_opentap .tool div span {
	display: inline-block;
	width: 4px;
	height: 4px;
	border-radius: 50%;
	background-color: #fff;
}
.left_opentap .tool div span:nth-child(2) {margin: 0 3px;}
.left_opentap .file_name {
	width: 100%;
	height: 28px;
	display: flex;
}
.left_opentap .file_name div {margin: 0 10px 0 10px;}
.left_opentap .file_name h2 {font-size: 16px;}
.left_opentap .file_list {
	display: flex;
	width: 100%;
	height: 37px;
	align-items: center;
	cursor:pointer;
	font-size: 14px;
}
.left_opentap .file_list div {margin: 0 10px 0 30px;}
.file_list.on {
	background-color: var(--body-bg);
}

/* top_opentab */
.top_opentab {
	background-color: var(--header-bg);
	border-bottom: 1px solid var(--line);
	width: calc(100% - 365px);
	height: 42px;
	position: fixed;
	top: 42px;
	left: 365px;
}
.top_opentab nav {height: 100%;}
.top_opentab nav ul {
	display: flex; 
	height: 100%;
}
.top_opentab nav ul li {
	height: 100%;
	display: flex;
	padding-left: 18px;
	border-right: 1px solid var(--line);
	line-height: 42px;
	font-size: 14px;
}
.top_opentab nav ul li > div:nth-child(1) {margin-right: 10px;}
.top_opentab nav ul li > p {cursor: pointer;}
.top_opentab nav ul li > div:nth-child(3) {margin: 0 20px 0 30px; cursor: pointer;}
.file_list_top.on {
	background-color: var(--body-bg);
	position: relative;
}
.file_list_top.on::before {
	content: '';
	width: 100%;
	height: 1px;
	position: absolute;
	background-color: var(--select-point);
	top: 0;
	left: 0;
}

/* 웹프로젝트 */
.web, .uiux, .profile, .design, .etc, .error {
	width: calc(100% - 365px);
	height: calc(100vh - 84px);
	background-color: var(--body-bg);
	margin: 84px 0 0 365px;
	overflow-y: scroll;
}
/* 스크롤바 커스텀 */
.web::-webkit-scrollbar,
.uiux::-webkit-scrollbar,
.profile::-webkit-scrollbar,
.design::-webkit-scrollbar,
.etc::-webkit-scrollbar{
	width: 17px;
	color: var(--body-bg);
	border-left: 1px solid #080B0E;
}
.web::-webkit-scrollbar-thumb, 
.uiux::-webkit-scrollbar-thumb, 
.profile::-webkit-scrollbar-thumb, 
.design::-webkit-scrollbar-thumb, 
.etc::-webkit-scrollbar-thumb {
	width: 17px;
	background-color: #434343;
}

/* 컨테이너 설정 */
.profile > .container, .web > .container, .uiux > .container, .design > .container, .etc > .container {
	width: 1395px;
	margin: 0 auto;
}
/* 프로필 */
.profile > .container h2 {
	font-size: 70px;
	text-align: center;
	margin: 0 auto;
	margin-top: 300px;
	margin-bottom: 490px;
}
.profile > .container .content {position: relative;}
.profile > .container .content h3 {
	font-size: 70px;
	font-weight: 700;
	color: var(--select-point3);
	margin-left: 118px;
	position: relative;
	z-index: 1;
}
.profile > .container .content .img {
	width: 414px;
	height: 1170px;
	background-color: var(--none-select);
	position: absolute;
	top: 44px;
	left: -80px;
}
.profile > .container .content .img img {
	position: absolute;
	top: 97px;
	left: 198px;
}
.profile > .container .content .text {
	margin-left: 590px;
	font-weight: 500;
}
.profile > .container .content .text h4 {
	font-size: 22px;
	font-weight: 500;
	color: var(--select-point);
}
/* name birth */
.profile > .container .content .text .about div:nth-child(2) {
	display: flex;
	margin-top: 30px;
	margin-bottom: 40px;
}
.profile > .container .content .text .about div:nth-child(2) p:nth-child(1) {
	margin-right: 58px;
	font-size: 20px;
}
.profile > .container .content .text .about div:nth-child(2) p:nth-child(2) {
	margin-right: 189px;
	font-size: 18px;
}
.profile > .container .content .text .about div:nth-child(2) p:nth-child(3) {
	margin-right: 59px;
	font-size: 20px;
}
.profile > .container .content .text .about div:nth-child(2) p:nth-child(4) {
	font-size: 18px;
}
/* phone email */
.profile > .container .content .text .about div:nth-child(3) {
	display: flex;
	margin-bottom: 40px;
}
.profile > .container .content .text .about div:nth-child(3) p:nth-child(1) {
	margin-right: 47px;
	font-size: 20px;
}
.profile > .container .content .text .about div:nth-child(3) p:nth-child(2) {
	margin-right: 101px;
	font-size: 18px;
}
.profile > .container .content .text .about div:nth-child(3) p:nth-child(3) {
	margin-right: 55px;
	font-size: 20px;
}
.profile > .container .content .text .about div:nth-child(3) p:nth-child(4) {
	font-size: 18px;
}
/* experience */
.profile > .container .content .text .experience h4 {
	margin-bottom: 30px;
}
.profile > .container .content .text .experience div:nth-child(2) {
	display: flex;
	margin-bottom: 20px;
}
.profile > .container .content .text .experience div:nth-child(2) p:first-child {
	font-size: 20px;
	margin-right: 161px;
}
.profile > .container .content .text .experience div:nth-child(3) {
	display: flex;
	margin-bottom: 20px;
}
.profile > .container .content .text .experience div:nth-child(3) p:first-child {
	font-size: 20px;
	margin-right: 103px;
}
.profile > .container .content .text .experience div:nth-child(4) {
	display: flex;
}
.profile > .container .content .text .experience div:nth-child(4) p:first-child {
	font-size: 20px;
	margin-right: 109px;
}
/* skill */
.profile > .container .content .text .skill {
	margin-top: 40px;
}
.profile > .container .content .text .skill > div:nth-child(2) {
	display: flex;
	gap: 25px;
	margin-top: 30px;
}
.profile > .container .content .text .skill div div{
	width: 50px;
	height: 50px;
}
.profile > .container .content .text .skill > div:nth-child(3) {
	display: flex;
	gap: 25px;
	margin-top: 20px;
}
/* contact */
.profile > .container .content .text .contact {
	margin-top: 40px;
	margin-bottom: 100px;
}
.profile > .container .content .text .contact > div {
	margin-top: 30px;
	display: flex;
}
.profile > .container .content .text .contact > div > div:first-child {
	margin-right: 91px;
}
.profile > .container .content .text .contact > div > div:last-child > div:nth-child(1) {
	display: flex;
	gap: 47px;
}
.profile > .container .content .text .contact > div > div:last-child > div:nth-child(2) {
	display: flex;
	gap: 55px;
	margin: 20px 0;
}
.profile > .container .content .text .contact > div > div:last-child > div:nth-child(3) {
	display: flex;
	gap: 44px;
	margin-bottom: 138px;
}
.profile > .container .content .text .contact > div > div:last-child > p {
	color: var(--select-point2);
	font-size: 18px;
}

